.categoriesDropdown {
    display: flex;
    flex-direction: column;
    position: absolute;
    left: 240px;
    top: 72px;
    background-color: #c1e8e3;
    z-index: 23;
    padding: 20px 0;
    border-radius: 10px;
}

.span {
    transform: rotate(45deg);
    position: absolute;
    top: -10px;
    left: 50px;
    height: 20px;
    width: 20px;
    background-color: #c1e8e3;
}

.category {
    margin: 0;
    padding: 5px 0;
    color: #003d29;
    padding: 8px 20px;
    text-decoration: none;
}

.category:hover {
    background-color: #f0f0f0;
}