@import url('https://fonts.googleapis.com/css2?family=Noto+Sans&family=Orbitron&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

.home {
    font-family: 'Noto Sans', sans-serif;
    font-family: 'Orbitron', sans-serif;
    font-family: 'Roboto', sans-serif;
}

.homeImg {
    width: 100%;
    height: 600px;
    z-index: -1;
    position: relative;
    top: -50px;
}

.content {
    position: absolute;
    top: 200px;
    width: 500px;
    padding: 0 100px;
}

.heading {
    font-size: 55px;
    font-weight: 900;
    color: #003d29;
}

.text {
    font-size: 18px;
}

.btn {
    cursor: pointer;
    background-color: #003d29;
    color: white;
    height: 45px;
    width: 130px;
    border-radius: 65px;
    border: 0;
    font-size: 16px;
}

.btn:hover {
    background-color: #11ab12;
}

/* Categories */

.categories {
    padding: 0 60px 30px 60px;
}

.categories_heading {
    font-size: 25px;
    font-weight: 600;
}

.category_list {
    display: flex;
    justify-content: space-between;
    padding: 0;
}

.categoryImage {
    height: 220px;
    width: 170px;
}

.categoryTitle {
    font-size: 20px;
    font-weight: 600;
    color: #003d29;
}

.category {
    text-align: center;
    list-style: none;
    height: 300px;
    width: 200px;
    z-index: 20;
    border-radius: 10px;
    cursor: pointer;
    transition: 0.3s;
    text-decoration: none;
}

.category:hover {
    transform: translateY(-1%);
}

/* Featured Products */

.featured {
    padding: 0 60px 30px 60px;
}

.featured_heading {
    font-size: 25px;
    font-weight: 600;
}

.featured_product_title {
    font-size: 15px;
    font-weight: 600;
    color: #003d29;
}

.productImage {
    height: 220px;
    width: 170px;
}

.featured_list {
    display: flex;
    justify-content: space-between;
    padding: 0;
    flex-wrap: wrap;
}

.featured_product {
    text-decoration: none;
    text-align: center;
    list-style: none;
    height: 300px;
    width: 250px;
    z-index: 20;
    border-radius: 10px;
    cursor: pointer;
    transition: 0.3s;
}

.featured_product:hover {
    transform: translateY(-1%);
}


/* Ad */

.ad {
    display: flex;
    padding: 30px 60px 60px 60px;
    height: 300px;
}

.adImg {
    border-radius: 15px 0 0 15px;
}

.ad_content {
    background-color: #003d29;
    padding: 20px 60px;
    color: white;
    border-radius: 0 15px 15px 0;
}

.limitedOffer {
    color: #b1bac5;
}

.offerData {
    font-size: 40px;
    font-weight: 500;
}

.adBtn {
    height: 40px;
    width: 130px;
    background-color: white;
    border: 0;
    font-size: 15px;
    font-weight: 700;
    color: #003d29;
    border-radius: 5px;
    cursor: pointer;
}
