@import url('https://fonts.googleapis.com/css2?family=Noto+Sans&family=Orbitron&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

.navbar {
    padding: 0px 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: 'Noto Sans', sans-serif;
    font-family: 'Orbitron', sans-serif;
    font-family: 'Roboto', sans-serif;
    font-weight: 600;
    font-size: 14px;
    height: 90px;
    background-color: white;
}

.navbar p {
    cursor: pointer;
}

.icon {
    height: 50px;
}

.categoryIcon {
    height: 11px;
    padding-left: 5px;
}

.name {
    display: flex;
    align-items: center;
    font-size: 25px;
    font-weight: bolder;
    color: #003d29;
    cursor: pointer;
    text-decoration: none;
}


.listItem {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-decoration: none;
    color: black;
} 

.search {
    width: 250px;
    border-radius: 50px;
    padding: 9px 15px;
    background-color: #eaecec;
    border: 0px;
}

.search:focus {
    outline: none;
}

.searchIcon {
    height: 18px;
    position: absolute;
    left: 973px;
    top: 39px;
}

.accountIcon {
    height: 20px;
    padding-right: 5px;
}

.cartIcon {
    height: 30px;
    padding-right: 5px;
}

.cartLen {
    position: relative;
    right: 51px;
    bottom: 3px;
    color: #f67303;
}