.empty {
    padding: 100px;
    font-size: 80px;
    font-weight: 800;
    color: #003d29;
}

.details {
    display: flex;
    padding: 50px 100px;
}

.productImg {
    height: 500px;
    max-width: 500px;
}

.content {
    padding-left: 50px;
}

.productTitle {
    margin: 0;
    font-size: 30px;
    font-weight: 600;
}

.productDes {
    font-size: 14px;
    color: grey;
    padding-bottom: 20px;
    text-align: justify;
}

.line {
    border: 1px solid black;
    width: 85%;
    opacity: 10%;
}

.price {
    padding-top: 20px;
    font-size: 30px;
    font-weight: 600;
    margin: 0;
}

.installment {
    margin: 10px 0;
    font-size: 14px;
    color: grey;
    padding-bottom: 20px;
}

.cartBtn {
    border: 0;
    height: 40px;
    width: 150px;
    color: #003d29;
    cursor: pointer;
    border-radius: 20px;
    margin-top: 20px;
    border: 1px solid #003d29;
    background-color: white;
    transition: 0.3s;
    font-weight: 600;
}

.cartBtn:hover {
    color: white;
    background-color: #003d29;
    border: 0;
}