.details {
    display: flex;
    padding: 50px 100px;
}

.productImg {
    height: 500px;
    max-width: 500px;
}

.content {
    padding-left: 50px;
}

.productTitle {
    margin: 0;
    font-size: 30px;
    font-weight: 600;
}

.productDes {
    font-size: 14px;
    color: grey;
    padding-bottom: 20px;
    text-align: justify;
}

.line {
    border: 1px solid black;
    width: 85%;
    opacity: 10%;
}

.price {
    padding-top: 20px;
    font-size: 30px;
    font-weight: 600;
    margin: 0;
}

.installment {
    margin: 10px 0;
    font-size: 14px;
    color: grey;
    padding-bottom: 20px;
}

.btns {
    display: flex;
    align-items: center;
    margin-top: 20px; 
}

.countSpan {
    color: #fb9954;
    font-weight: 600;
}

.productCount {
    font-size: 14px;
    font-weight: 400;
    padding-left: 30px;
    width: 150px;
}

.btn1 {
    cursor: pointer;
    border: 0;
    padding: 0;
    height: 50px;
    width: 50px;
    font-size: 25px;
    font-weight: 100;
    color: #003d29;
    border-radius: 25px 0 0 25px;
    padding-left: 10px;
}

.btn2 {
    cursor: pointer;
    border: 0;
    padding: 0;
    height: 50px;
    width: 50px;
    font-size: 25px;
    font-weight: 100;
    color: #003d29;
}

.btn3 {
    cursor: pointer;
    border: 0;
    padding: 0;
    height: 50px;
    width: 50px;
    font-size: 25px;
    font-weight: 100;
    color: #003d29;
    border-radius: 0 25px 25px 0;
    padding-right: 10px;
}

.buyBtn {
    border: 0;
    height: 40px;
    width: 150px;
    color: white;
    cursor: pointer;
    border-radius: 20px;
    background-color: #003d29;
    margin: 20px 20px 0 0;
    transition: 0.3s;
    font-weight: 600;
} 

.buyBtn:hover {
    color: #003d29;
    border: 1px solid #003d29;
    background-color: white;
}

.cartBtn {
    border: 0;
    height: 40px;
    width: 150px;
    color: #003d29;
    cursor: pointer;
    border-radius: 20px;
    margin-top: 20px;
    border: 1px solid #003d29;
    background-color: white;
    transition: 0.3s;
    font-weight: 600;
}

.cartBtn:hover {
    color: white;
    background-color: #003d29;
    border: 0;
}

.addedToCart {
    padding-left: 10px;
    font-size: 20px;
    font-weight: 500;
    color: #ff7006;
}