.categoryPage {
    padding: 0 60px;
}

.ad {
    display: flex;
    justify-content: space-between;
    height: 300px;
    background-color: #faf0e4;
}

.content {
    padding-left: 80px;
}

.discount {
    color: #003d29;
    font-size: 45px;
    font-weight: 600;
}

.categoryPageImg {
    padding-right: 150px;
}

.adBtn {
    border: 0;
    cursor: pointer;
    font-size: 16px;
    background-color: #003d29;
    color: white;
    height: 46px;
    width: 120px;
    border-radius: 23px;
}

.productsHeading {
    padding-top: 50px;
    font-size: 30px;
    font-weight: 600;
}

.productList {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.product {
    text-decoration: none;
    color: black;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    width: 300px;
    height: 400px;
    justify-content: space-between;
    margin-bottom: 60px;
    transition: 0.6s;
}

.product:hover {
    transform: translateY(-1%);
}


.productDetails {
    display: flex;
    justify-content: space-between;
    font-weight: 600;
}

.price {
    display: flex;
    padding-left: 10px;
}

.priceSpan {
    font-size: 11px;
}

.productImg {
    width: 100%;
    height: 60%;
    transition: 0.3s;
}

.productImg:hover {
    transform: translateY(-2%);
}

.cartBtn {
    width: 50%;
    height: 40px;
    border-radius: 20px;
    border: 2px solid black;
    background-color: white;
    font-size: 16px;
    cursor: pointer;
    transition: 0.3s;
}

.cartBtn:hover {
    background-color: #003d29;
    color: white;
    border: 0;
}

.servicesHeading {
    margin: 0;
    font-size: 30px;
    font-weight: 600;
}

.services {
    display: flex;
    justify-content: space-between;
    margin: 40px 0;
}

.service {
    display: flex;
    flex-direction: column;
    background-color: #f6f6f6;
    /* height: 600px; */
    width: 360px;
    border-radius: 20px;
    cursor: pointer;
    box-shadow: 0 0.1rem 0.3rem #4d4e52;
    transition: 0.3s;
}

.service:hover {
    transform: translateY(-1%);
    box-shadow: 0 0.5rem 1rem #4d4e52;
}

.serviceContent {
    padding: 30px;
}

.serviceTitle {
    font-size: 30px;
    font-weight: 500;
    margin: 0;
}

.serviceImg {
    height: 300px;
    width: 360px;
    border-radius: 0 0 20px 20px;
}
